import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { isIE } from 'react-device-detect'
import {
  Menu,
  Image,
  Card,
  Breadcrumb,
  Segment,
  Label,
} from 'semantic-ui-react'
import { getWeather } from '../reducers/weatherReducer'
import { getTimeZone } from '../reducers/timeZoneReducer'
import { initializeCountries } from '../reducers/countryReducer'
import { getCountryData } from '../reducers/countryDataReducer'
import {
  filterCountriesByRegion,
  filterCountriesBySubRegion,
} from '../reducers/countryReducer'
import zoomLevel from '../zoomLevels'
import loadable from '@loadable/component'
import CiaData from '../components/CiaData'
import WeatherIE from '../components/WeatherIE'

const Map = loadable(() => import('../pages/Map'))
const Weather = loadable(() => import('../components/Weather'))

const NoData = () => (
  <Label>
    <h2>{'No data available'} </h2>{' '}
  </Label>
)

const Country1 = ({ data }) => {
  const [activeTab, setActiveTab] = useState('Map')

  const dispatch = useDispatch()

  const unit = useSelector((state) => state.unit.unit)

  const countriesData = useSelector((state) => state.countriesData.data)

  const location =
    data.capitalInfo.latlng === undefined
      ? data.latlng
      : data.capitalInfo.latlng

  useEffect(() => {
    if (activeTab === 'CoatOfArms') {
      console.log('activeTab', activeTab)
    }
  }, [activeTab])

  useEffect(() => {
    dispatch(getCountryData(data.name.common))
  }, [data])

  useEffect(() => {
    dispatch(
      getWeather({
        location: location,
        unit: unit,
      })
    )
    dispatch(getTimeZone({ location: location }))
  }, [unit])

  const handleTabClick = (e, { name }) => {
    setActiveTab(name)
  }

  return (
    <>
     <Segment attached="bottom">
      <Menu id="breadcrumb"  secondary>
        <Breadcrumb size="small">
          <Breadcrumb.Section
            key="All"
            link
            onClick={() => dispatch(initializeCountries())}
          >
            All
          </Breadcrumb.Section>
          <Breadcrumb.Divider icon="right chevron" />
          <Breadcrumb.Section
            key={data.region}
            link
            onClick={() => dispatch(filterCountriesByRegion(data.region))}
          >
            {data.region}
          </Breadcrumb.Section>
          {data.subregion !== '' ? (
            <>
              <Breadcrumb.Divider icon="right chevron" />
              <Breadcrumb.Section
                key={data.subregion}
                link
                onClick={() =>
                  dispatch(filterCountriesBySubRegion(data.subregion))
                }
              >
                {data.subregion}
              </Breadcrumb.Section>
              <Breadcrumb.Divider icon="right chevron" />
              <Breadcrumb.Section key={data.name.common} active>
                {data.name.common}
              </Breadcrumb.Section>
            </>
          ) : (
            <></>
          )}
        </Breadcrumb>
      </Menu>
      <Menu id="country-tabs" attached pointing secondary>
        <Menu.Item
          name="Map"
          active={activeTab === 'Map'}
          onClick={handleTabClick}
        />
        <Menu.Item
          name="Flag"
          active={activeTab === 'Flag'}
          onClick={handleTabClick}
        />{' '}
        <Menu.Item
          name="CoatOfArms"
          active={activeTab === 'CoatOfArms'}
          onClick={handleTabClick}
        />
        <Menu.Item
          name="Weather"
          active={activeTab === 'Weather'}
          onClick={handleTabClick}
        />
        <Menu.Item
          name="CIA"
          active={activeTab === 'CIA'}
          onClick={handleTabClick}
        />
      </Menu>
      {activeTab === 'Map' ? (
        <>
          <Map lt={data.latlng[0]} lg={data.latlng[1]} zm={zoomLevel(data)} />
        </>
      ) : (
        <></>
      )}
      {activeTab === 'Flag' ? (
        <Image
          id="flag"
          centered
          src={isIE ? data.flags.png : data.flags.svg}
          alt="country flag"
          size="medium"
          bordered
        />
      ) : (
        <></>
      )}
      {activeTab === 'CoatOfArms' ? (
        <>
          {Object.entries(data.coatOfArms).length === 0 ? (
            <Label>No image available</Label>
          ) : (
            <Image
              id="coatOfArms"
              centered
              src={isIE ? data.coatOfArms.png : data.coatOfArms.svg}
              alt="coat of arms"
              size="medium"
              bordered
            />
          )}
        </>
      ) : (
        <></>
      )}
      {activeTab === 'Weather' ? (
        <>
          <Card id="CiaCard" fluid>
            {isIE ? <WeatherIE country={data} /> : <Weather country={data} />}
          </Card>
        </>
      ) : (
        <></>
      )}
      {activeTab === 'CIA' ? (
        <>
          <Card id="CiaCard" fluid>
            {countriesData === 'no data' ? (
              <NoData />
            ) : (
              <CiaData country={data.name.common} />
            )}
          </Card>
        </>
      ) : (
        <></>
      )}
      </Segment>
    </>
  )
}

const Country = React.memo(Country1)

export default Country
